export default{
	data(){
		return{
			show:0
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		activation(){
			uni.showModal({
				content:'马上激活',
				success(res) {
					if(res.confirm){
						uni.navigateTo({
							url:'../planpage/activation'
						})
					}
				}
			})
		}
	}
}
